/* Global Styles */
body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f1f5f9;
  margin: 0;
  text-align: center;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  height: 100vh;
  color: #333;
}

h1 {
  color: #4A90E2;
  margin-top: 50px;
  font-size: 36px;
  text-align: center;
}

h2 {
  color: #7F8C8D;
  margin-top: 20px;
  font-size: 24px;
}

/* Form Styles */
form {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
  width: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

input {
  width: 100%;
  padding: 12px;
  margin-bottom: 15px;
  border-radius: 5px;
  border: 1px solid #ddd;
  font-size: 16px;
}

button {
  width: 50%;
  padding: 12px;
  background-color: #5C6BC0;
  border:2px solid #3F51B5;
  color: white;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #3F51B5;
}

/* Disaster List Styles */
.disaster-list {
  list-style-type: none;
  padding: 0;
  margin-top: 40px;
  width: 100%;
  max-width: 900px;
}

.disaster-list li {
  background-color: #fff;
  padding: 15px;
  margin-bottom: 12px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  transition: transform 0.3s ease;
}

.disaster-list li:hover {
  transform: translateY(-5px);
}

.disaster-list button {
  background-color: #E74C3C;
  margin: 5px;
  border: 2px solid #C0392B;
  color: white;
  padding: 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s ease;
  width: 80px;
  margin-bottom: 8px;
}

.disaster-list button:hover {
  background-color: #C0392B;
}

/* Edit Form Styles */
.edit-disaster-form {
  display: none;
  width: 450px;
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Utility Styles */
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 20px;
}

.container h1 {
  margin-top: 20px;
}